/*Small Laptops*/
@media screen
and (min-width: 800px) and (max-width: 1023px)
and (min-height: 600px) and (max-height: 700px){
    /*.StoryView {height: 87vh;}*/
    .accordion-item .body {height: 28vh; font-size:.8em;}
    /* Height for story */
    .story {
        overflow-y: scroll;
        height:32vh;
    }
    /* Story Viewer Options (buttons) */
    .story-viewer-options .button{font-size:.6em;}
    /* Height for annotations */
    .card.annotation, .relatedStories {height: 23vh; font-size:.9em;}
}
/*Average Laptops*/
@media screen
and (min-width: 1024px)
and (min-height: 700px) and (max-height: 768px){
    /*Height of accordion on the left*/
    .accordion-item .body {height: 25vh;}

    /* Height for story */
    .story {
        overflow-y: scroll;
        height:35vh;
    }
    /* Height for annotations */
    .card.annotation, .relatedStories {height: 23vh; font-size:1em;}

    /* Story Viewer Options (buttons) */
    .story-viewer-options .button{font-size:.8em;}

    .accordion-item .accordion-title {font-size: 1.3em;}
    .accordion-item .body, .keyword-well{font-size:.9em;}
}

/*Tall Laptops*/
@media screen
    and (min-height: 769px){
    /*Height of accordion on the left*/
    .accordion-item .body {height: 32.5vh;}

    /* Height for story */
    .story {
        overflow-y: scroll;
        height:37vh;
    }
    /* Height for annotations */
    .card.annotation, .relatedStories {height: 26vh; font-size:1em;}

    /* Story Viewer Options (buttons) */
    .story-viewer-options .button{font-size:.9em;}

    .accordion-item .accordion-title {font-size: 1.3em;}
    .accordion-item .body, .keyword-well{font-size:.9em;}
}

.accordion-item .body {
    padding:5%;
    width:100%;
    border:1px solid #e7e7e7;
    overflow-y:scroll;
}

.keyword-well {
    background-color: #e3e3e3;
    padding:3px;
    border-radius:2px;
    margin:2px;
    display: inline-block;
    color: black;
}

.story-viewer {
    border:2px solid #e3e3e3;
    padding:1% 0 0 0;
    margin-left:1.5%;
    width:97%;
    background:white;
}

.story-viewer-options {
    list-style: none;
    margin:0;
}

.story-viewer-options li {
    display:inline-block;
}

/* Height for story */
.story.card {height: 90%;}

/* Height for Annotations */
.card.annotation .card-section {
    overflow-y: scroll;
    height: 70%;
}

.title {
    background-color: #dcdcdc;
    padding: 1% 3% 1% 3%;
}
h2.title{font-weight:bold;}

.relatedStories {
    border: 2px solid #e3e3e3;
    overflow-y: scroll;
    background:white;
}

.associated-items {
    color: #44739c;
}
